.infoBar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #2979FF;
    border-radius: 4px 4px 0 0;
    height: 60px;
    width: 100%;
  }
  
  .leftInnerContainer {
    flex: 0.5;
    display: flex;
    align-items: center;
    margin-left: 5%;
    color: white;
    width:33%;
  }
  
  .middleInnerContainer{
    display: flex;
    flex: 0.5;
    justify-content: flex-end;
    margin-right: 5%;
    width:55%;
  }
  .rightInnerContainer {
    display: flex;
    flex: 0.5;
    justify-content: flex-end;
    margin-right: 5%;
    width:15%;
   
  }
  
  .onlineIcon {
    margin-right: 5%;
  }
  .signOutButton{
    color:white;
  }
  .shareButton{
    color:white;
    background-color: #2979FF;
    border: none;
   }
  .hrefHolder{
    color:black;
    background-color: white;
    width:70%
  }